.resume-img {
    width: 25vw;
    border-radius: 8px;
}

.resume-card {
    box-shadow: 
        0px 4px 10px 1px rgba(0, 0, 0, 0.123),
        0px 4px 10px 1px rgb(0, 0, 0, 0.123),
        0px 6px 10px 1px rgb(0, 0, 0, 0.123);
}

.resume-card-body {
    display: flex;
    justify-content: space-evenly;
    align-self: center;
    align-items: center;
    flex: 1 0 0 calc(25% - 1em);
    margin: 0 8vw; 
    width: 75vw;
    height: 75vh;
    border-radius:12px;
    color: rgb(109,105,95);
    background-color: rgba(0, 0, 0, 0.562);
    box-shadow: 
      0px 2px 5px 1px rgba(255, 255, 255, 0.051),
      0px 4px 12px 1px rgba(255, 255, 255, 0.151),
      0px 6px 15px 1px rgba(87, 87, 87, 0.121),
      0px 8px 18px 1px rgba(82, 87, 87, 0.151),
      0px 10px 20px 1px rgba(82, 87, 87, 0.181);
}

.resume {
    height: 90vh;
    background-color: rgba(0, 0, 0, 0.774);  
    display: flex;
    justify-content: center;
}

.skillsCard {
    display: flex;
}

.resume-title {
    position: absolute;
    top: 20.5vh;
}

h5, .resume-title, .languages, .tools {
    color: rgb(109,105,95);
    text-shadow: 3px 2px 2px rgb(0, 0, 0);
}

.skillsCard ul {
    position: relative;
    top: -6vh;
    padding: 0 2vw;
}

ul li {
    font-size: 1.2vw;
    list-style: inside square;
    position: relative;
    padding: 0 0 0 2vw;
}